// Movie

//
// Common
// __________________

.movies {
  .movie {
    background-color: #ffffff;
    margin: 0 15px 70px;
    padding: 20px;
    border-radius: 5px;
    color: #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.25);

    a {
      display: grid;
      grid-template-columns: minmax(150px, 1fr) 2fr;
      grid-gap: 20px;
      text-decoration: none;
      color: inherit;
    }

    img {
      position: relative;
      display: block;
      max-width: 150px;
      width: 100%;
      margin-right: 30px;
      margin-bottom: 50px;
      box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3),
        0 -12px 36px -8px rgba(0, 0, 0, 0.025);
    }

    &__title,
    &__year {
      margin: 0;
    }

    &__title {
      margin-bottom: 5px;
      font-size: 24px;
      color: #2c2c2c;
    }

    &__genres {
      list-style: none;
      padding: 0;
      margin: 0;
      display: flex;
      flex-wrap: wrap;
      margin: 5px 0;
    }

    &__genres li,
    &__year {
      margin-right: 10px;
      font-size: 14px;
    }
  }
}

//
// Mobile
// __________________

@media screen and (max-width: 767px) {
  .movies {
    .movie {
      width: 100%;
      margin: 0 0 50px;

      a {
        grid-template-columns: minmax(150px, 1fr);
      }

      img {
        margin: 0;
      }
    }
  }
}
